<template>
  <swiper class="detail-swiper">
    <swiper-item v-for="(item, index) in imgChangeData" :key="index">
      <img :src="item" alt="">
    </swiper-item>
  </swiper>
</template>

<script>
import {Swiper, SwiperItem} from 'components/common/swiper'

export default {
  name: "DetailSwiper",
  components: { Swiper,SwiperItem },
  props: {
    topImages: {
      type: Array,
      default() {
        return []
      }
    }
  },
  computed: {
    imgChangeData() {
      return this.topImages.map(item => "https:" + item)
    }
  },
  data() {
    return {
    }
  }
}
</script>

<style scoped>
  .detail-swiper {
    height: 300px;
    overflow: hidden;
  }
</style>